label {
  display: block;
  margin-top: 10px;
}

.card-container.card {
  max-width: 350px !important;
  padding: 40px 40px;
}

.card {
  background-color: lightseagreen;
  padding: 20px 25px 30px;
  margin: 0 auto 25px;
  margin-top: 50px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.btn {
  margin: 10px;
}

.navbar {
  background-color: white;
}

.nav-link,
.nav-link:hover,
.nav-link:active,
.nav-link:visited {
  color: lightseagreen;
  font-weight: bold;
}

.navbar-brand,
.navbar-brand:hover,
.navbar-brand:active,
.navbar-brand:visited {
  color: lightseagreen;
  font-weight: bold;
}

.commentUserName {
  font-weight: bold;
}